@charset "UTF-8";
/* Generic styles */
.datepicker .icon-arrow-right,
.datepicker .icon-arrow-left {
  width: 100%;
  height: 100%;
  display: block;
  position: relative; }
  .datepicker .icon-arrow-right:after,
  .datepicker .icon-arrow-left:after {
    position: absolute;
    top: 0;
    left: 0;
    width: 100%;
    height: 100%;
    display: block;
    color: #000;
    font-style: normal;
    font-size: 30px;
    line-height: 20px; }

.datepicker .icon-arrow-left:after {
  content: "◂"; }

.datepicker .icon-arrow-right:after {
  content: "▸"; }

/* Dashboard styles */
.container.dashboard .panel-heading {
  position: relative; }
  .container.dashboard .panel-heading .pagination-nav {
    position: absolute;
    right: 5px;
    top: 5px; }
    .container.dashboard .panel-heading .pagination-nav ul.pagination {
      margin: 0;
      display: block; }
      .container.dashboard .panel-heading .pagination-nav ul.pagination li span,
      .container.dashboard .panel-heading .pagination-nav ul.pagination li a {
        padding: 2px 8px;
        line-height: 21px; }

.container.dashboard .edit-line-template {
  display: none; }
  .container.dashboard .edit-line-template.shown {
    display: table-row;
    background: #e6edef;
    outline: 1px solid rgba(169, 182, 185, 0.28);
    box-shadow: 0px 0px 20px 1px rgba(67, 77, 80, 0.37); }

.container.dashboard .entry-text-label {
  white-space: nowrap;
  overflow: hidden;
  text-overflow: ellipsis;
  max-width: 170px; }

.container.dashboard .actions-td {
  text-align: center; }
  .container.dashboard .actions-td a {
    display: inline-block;
    margin: 0 5px; }

.container.dashboard .edit-actions {
  text-align: center;
  min-width: 100px; }
  .container.dashboard .edit-actions a, .container.dashboard .edit-actions button {
    margin-top: 4px; }
